.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin 5s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  0% {
    transform: translateX(-600px);
  }
  50% {
    transform: translateX(1000px);
  }
  100% {
    transform: translateX(0px);
  }
}

.Songs-songs, .Songs-song {
  color: black;
}

.Songs-verse {
  color: pink;
}

.Songs-chorus {
  color: blue;
}

.Songs-bridge {
  color: green;
}

.Songs-read {
  color: green;
}

.Songs-unread {
  color: red;
}

.Songs-verse, .Songs-chorus, .Songs-bridge, .Songs-part-c, .Songs-outro, .Songs-intro, .Gigs-song, .Gigs-gig{
  text-align: left;
}

.App-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
}

.Gigs-images-container {
  display: flex;
}

.Gigs-img {
  width: 33%;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}